:root{
    --liftWidth: 0%;
    --floorHeight: 0%;
    --transitionTime: 0s;
}
.mainContainer{
    height: 100vh;
}

.LiftContainer{
    height: 90%;
    width: 70%;
}
.innerLiftContainer{
    height: 95%;
    width: 95%;
}
.floor{
    height: var(--floorHeight);
    width: 100%;
}
.floorBox{
    width: 85%;
    padding: 3%;
    padding-left: 5%;
}
.floorTitle{
    width: 15%;
    font-weight: bold;
}
.innerFloorBox{
    height: 85%;
    padding-bottom: 2%;
}
.baseLine{
    height: 3px;
    background-color: black;
}
.buttonBox{
    margin-left: -2%;
    width: 8%;
}
.upButton{
    margin-bottom: 10%;
    border: none;
    background-color: yellowgreen;
    width: 100%;
}
.downButton{
    border: none;
    background-color: yellow;
    width: 100%;
}
.title{
    margin-bottom: 15%;
}
.lifts{
    width: 90%;
}
.lift{
    width: 10%;
    margin-top: -3%;
    background-color: blue;
    position: relative;
    top: 0px;
    transition: top ease var(--transitionTime);
}